import { Component, OnInit, ViewChild } from '@angular/core';
import { Categoria } from '../../models/categoria';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DataTableDirective } from 'angular-datatables';
import { CategoriaService } from '../../service/categoria.service';
import Swal from 'sweetalert2';
import { Subject } from 'rxjs';
import { isCaracter } from 'src/app/constantes/constantesproyecto';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Equipo } from 'src/app/pages/equipo/models/equipo';

@Component({
  selector: 'app-listar-categoria',
  templateUrl: './listar-categoria.component.html',
  styleUrls: ['./listar-categoria.component.scss']
})
export class ListarCategoriaComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  categorias: Categoria[] = []
  equipos: Equipo[] = []
  categoria: Categoria
  formulario !: FormGroup
  showLoading = false;
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Categoria> = new Subject<Categoria>();



  constructor(
    private modalService: NgbModal,//para abrir el modal openModal()
    private categoriaService: CategoriaService,
    private fb: FormBuilder
  ) { this.formulario = this.inifc() }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Listado' }];
    /**table */
    this.dtOptions = {
      columnDefs: [

        { orderable: false, targets: [0, 2] },
        { searchable: false, targets: [0, 2] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatos()

  }

  private inifc(): FormGroup {
    return this.fb.group({
      nombre: ['', [Validators.required, Validators.pattern(isCaracter)]]
    })
  }

  cargarDatos() {
    this.categoriaService.categorias().subscribe((resp: any) => {
      this.categorias = resp;
      this.dtTrigger.next(null)
      this.categoria = null
    });
    this.categoriaService.equipos().subscribe(data => {
      this.equipos = data
    })

  }

  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  push() {
    if (this.categoria != null) {
      this.editar()
    } else {
      this.guardar()
    }
  }

  guardar() {
    this.showLoading = true
    this.categoria = new Categoria(this.formulario.get('nombre').value)
    if (this.existe(this.categoria.nombre.toLowerCase() || this.categoria.nombre.toUpperCase())) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text: 'Ya existe la categoría',
        icon: 'warning',
      });
      this.formulario.reset()
      this.reload()

    } else {
      this.categoriaService.guardarCategoria(this.categoria).subscribe(resp => {
        this.showLoading = false
        if (resp) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Almacenado correctamente`,
            icon: 'success',
          });
          this.reload()
          this.formulario.reset()
        } else {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: `Ocurrio un problema`,
            icon: 'warning',
          });
        }
      })
    }
    this.formulario.reset()
    return Object.values(this.formulario.controls)
      .forEach(control =>
        control.markAsTouched())
  }


  paraEdit(x: Categoria) {
    this.formulario.patchValue(x)
    this.categoria = x
  }

  editar() {
    this.showLoading = true
    const nuevoNombre = this.formulario.controls['nombre'].value
    if (nuevoNombre === this.categoria.nombre) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Éxito',
        text: 'No se realizaron cambios.',
        icon: 'success',
      });
      this.formulario.reset()
      this.categoria = null
    } else {
      if (this.existe(nuevoNombre)) {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ya existe la categoría`,
          icon: 'warning',
        });
        this.formulario.reset()
        this.categoria = null
      }
      else {
        this.categoria.nombre = nuevoNombre
        this.categoriaService.editar(this.categoria).subscribe(resp => {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Modificado correctamente`,
            icon: 'success',
          });
          this.reload()
          this.formulario.get('nombre').setValue("")
        }, (err) => {
          this.showLoading = false
          this.reload()
          this.reload()
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Error',
            text: `Algo fallo`,
            icon: 'error',
          });
        })
        this.formulario.reset()
      }
    }
  }




  eliminar(x: Categoria) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Sí, borrar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {

          this.categoriaService.eliminar(x.id).subscribe((response) => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Eliminado",
              text: `El registro ha sido eliminado`,
              icon: "success",
            });
            this.formulario.reset();
            this.reload();
          },
            (err: any) => {
              Swal.fire({
                confirmButtonColor: "#a90000",
                title: "Advertencia",
                text: `El registro no puede ser eliminado`,
                icon: "warning",
              });
            });

      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: "Información",
          text: `Cambios no aplicados`,
          icon: "info",
        });
      }
    });
  }

  verificar(id: number): boolean {
    return this.equipos.some(eq => eq.categoria.id === id)
  }

  /**Para recargar */
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.categorias = []
      dtInstance.destroy()
      this.cargarDatos()
    })
  }

  existe(nombre: string): boolean {
    const normalizedNombre = nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    return this.categorias.some(data => {
      const normalizedCarrera = data.nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
      return normalizedCarrera.toLocaleLowerCase() === normalizedNombre.toLocaleLowerCase();
    });
  }

  //PARA LA AYUDA
  openModalA(content) {
    this.modalService.open(content, {
      keyboard: false,
      scrollable: true, size: 'lg'
    });
  }


}
